#root{
    width: 100%;
}
*{
    margin: 0;
    padding: 0;
    scroll-behavior: smooth;
}

body{
    display: flex;
    flex-direction: column;
    align-items: center;
    /* background: radial-gradient(
        rgb(83, 192, 255), 
        rgb(13, 122, 165)
    );  */
}

h1{
    color: #ffeedd;
}

h2{
    color: #ffeedd;
}

/* p{
    color: #ffeedd;
} */

a{
    color: #ffeedd;
    text-decoration: none;
}

.Home_home__P1XcU{
    width: 100%;
    height: 100vh;
    background-color: #628199;
}
.Blog_blog__3CeKQ{
    width: 100%;
    background-color: #628199;
}
.Top_top__3y-BO{
    width:100%;
    height:100vh;
    /* background-color: #628199; */
}

.Top_title__2lbQs{
    padding-top: 5%;
    display: flex;
    justify-content: center;
    text-shadow: 4px 4px 2px rgba(0,0,0,.5);
}

.Top_table__2Sy_2{
    padding-top: 5%;
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    align-items: center;
}

.Top_section-title__2rIwj{
    display: flex;
    justify-content: center;
    text-shadow: 3px 3px 1px rgba(0,0,0,.5);
}

.Top_ads__7yNx8{
    width: 60%;
    padding-top: 2%;
    display: flex;
    justify-content: space-between;
}

.Top_ad__3w_a-{
    width: 20%;
    font-size: 20px;
    font-weight: bold;
}
.Top_ad__3w_a-:hover{
    text-decoration: underline;
}

.Top_greeting__1l7kD{
    padding-top: 5%;
    text-align: center;
}
.Top_greeting__1l7kD p{
    padding-top: 2%;
    color: #ffeedd;
}
.Profile_profile__R5G2v{
    width: 100%;
    height:100vh;
    display: flex;
    background-color: #ffffff;
}

.Profile_profile-left__3GeEC{
    width: 50%;
    height: 100vh;
    margin-left: 5%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.Profile_profile-right__2oUG7{
    width: 75%;
    height:100vh;
    margin-right: 10%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
}

.Profile_profile-img__2ZC0r{
    width: 17.5vw;
    height: 17.5vw;
    border-radius: 50%; /* 画像の四隅を丸くする */
    object-fit: cover;
}

.Profile_profile-img-description__3pHsM{
    padding-top: 10px;
    font-size: 15px;
}

.Profile_introduce__1uNSH{
    font-size: 16px;
}
.Profile_introduce__1uNSH span{
    font-size: 32px;
    font-weight: bold;
}
.Products_products__rvxoH{
    width: 100%;
    height: 100vh;
    padding-top: 5%;
    text-align: center;
    background-color: #628199;
}
.Products_products__rvxoH p{
    color: #ffeedd;
}

.Products_section-title__1ueGK{
    display: flex;
    justify-content: center;
    text-shadow: 3px 3px 1px rgba(0,0,0,.5);
}

.Products_grid__1B13Z{
    width: 80%;
    margin: 5% auto;
    display: grid;
    grid-gap: 5%;
    place-items: center;
    grid-template-rows: repeat(1, 200px);
    grid-template-columns: repeat(4, 1fr);
}

.Products_tile-title__2wdkY{
    margin: 5%;
}

.Products_tile__1c9UA{
    padding: 5%;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border: 4px solid #ffeedd;
    box-shadow: 2px 2px 1px rgba(0,0,0,.5);
    text-shadow: 1px 1px 1px rgba(0,0,0,.75);
}.Products_tile__1c9UA div{ 
    display: none;
}.Products_tile__1c9UA p{
    color: #221111;
}

.Products_tile__1c9UA:hover{
    color: #221111;
    background-color: #ffeedd;
    border: 4px solid #221111;
    box-shadow: 2px 2px 1px rgba(255,255,255,.5);
    text-shadow: 1px 1px 1px rgba(255,255,255,.75);
}.Products_tile__1c9UA:hover div{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.Navigation_navigation__3El1m{
    position: fixed;
    top: 10%;
    right: 5%;
    display:flex;
    flex-direction: column;
    z-index: 3;
}

.Navigation_flow-btn__24Egq{
    margin: 20% 0;
    position:relative;
    display:inline-block;
    width:50px;
    height:50px;
    font-size:30px;
    border-radius:50%;
    text-decoration:none;
    transition:.5s;
}

.Navigation_flow-btn__24Egq i{
    position:absolute;
    top:50%;
    left:50%;
    -webkit-transform:translate(-50%,-50%);
    transform:translate(-50%,-50%);
}




/* Change Language */
.Navigation_language-btn__1Q8XV {
    margin-bottom: 250%;            /* language button と SNS button の間はスペースを開ける */
    border:solid 2px #ff8844;
    color: #ff8844;
    background-color: #ffffff;
}
/* Languageマウスホバー時 */
.Navigation_language-btn__1Q8XV:hover{
    color: #ffffff;
    border:solid 2px #ff8844;
    background:#ff8844;
}

